import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Button, Input } from 'reactstrap';

import { SearchContainer } from './style';

const propTypes = {};

const defaultProps = {};

export default function SearchSection(props) {
  return (
    <SearchContainer>
      <Row>
        <Col xs="3" className="no-padding">
          <div className="vertical-aligned">
            <Button color="primary"><span>&#8249;</span> Back</Button>
          </div>
        </Col>
        <Col xs="9" className="no-padding">
          <Input type="email" name="email" id="exampleEmail" placeholder="Search for a movie" />
        </Col>
      </Row>
    </SearchContainer>
  );
}

SearchSection.propTypes = propTypes;
SearchSection.defaultProps = defaultProps;